body {
  margin: 0;

  background: #edf2f4;

  font-family: 'Exo 2', sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul,
li {
  display: flex;

  margin: 0;
  padding: 0;

  text-indent: 0;
  
  list-style-type: none;
}

button {
  border: none;
  padding: 0;

  font-family: 'Exo 2', sans-serif;
  
  background-color: transparent;
  cursor: pointer;
}

p, h1, h2, h3 {
  margin: 0;
}

a:active,
a:hover,
a {
  text-decoration: none;
  color: #8d99ae;
  cursor: pointer;
}

