/* Стили для ширины, которая меньше 649px (включительно) 
    Здесь хранятся правила для устройств с маленьким экраном.
    Вертикальная ориентация. 
*/
@media only screen and (max-width: 649px) {}

/* Стили для ширины, которая от 650px до 767px (включительно) 
    Здесь хранятся правила для устройств с маленьким экраном.
    Горизонтальная ориентация. 
*/
@media (min-width: 650px) and (max-width: 767px) {}

/* Стили для ширины, которая меньше 767px (включительно) 
    Здесь хранятся правила, которые совпадают
    и в вертикальной, и в горизонтальной ориентации 
    устройств с маленьким экраном.
*/
@media only screen and (max-width: 767px) {
  .about {
    flex-direction: column;
  }

  .photo {
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;

    width: 300px;
    height: 300px;
  }

  .about-container{
    margin: 50px 30px 0;
  }

  .about-title {
    padding-bottom: 20px;
    
    font-size: 15px;
  }

  .about-text__first-paragraph {
    padding-bottom: 15px;

    font-size: 15px;
  }

  .about-text__subsequent-paragraph {
    padding-bottom: 15px;

    font-size: 13px;
  }

  .resume-link-container {
    padding: 30px 0 50px 0;
  }

  .resume-link {
    width: 180px;
    height: 40px;

    font-size: 10px;

    border-radius: 22px;
  }
}

/* Стили для ширины, которая от 768px до 1023px (включительно) 
    Здесь хранятся правила для устройств со средним экраном.
    Вертикальная ориентация.
*/
@media (min-width: 768px) and (max-width: 1023px) {
  .about {
    flex-direction: column;
  }
  
  .photo {
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
  }

  .about-container{
    margin: 50px 150px 0;
  }
}

/* Стили для ширины, которая от 1024px до 1599px (включительно) 
    Здесь хранятся правила для устройств со средним экраном.
    Горизонтальная ориентация.
*/
@media (min-width: 1024px) and (max-width: 1599px) {
  .photo {
    margin: 50px 0 0 150px;
    padding: 0 50px 0 0;
  }

  .about-container{
    margin: 50px 150px 0 0;
  }
}

/* Стили для ширины, которая от 768px до 1599px (включительно) 
    Здесь хранятся правила, которые совпадают 
    и в вертикальной, и в горизонтальной ориентации 
    устройств со средним экраном.
*/
@media (min-width: 768px) and (max-width: 1599px) {
  .photo {
    width: 300px;
    height: 300px;
  }

  .about-title {
    padding-bottom: 30px;
    
    font-size: 18px;
  }

  .about-text__first-paragraph {
    padding-bottom: 15px;

    font-size: 18px;
  }

  .about-text__subsequent-paragraph {
    padding-bottom: 15px;

    font-size: 15px;
  }

  .resume-link-container {
    padding: 30px 0 50px 0;
  }

  .resume-link {
    width: 230px;
    height: 50px;

    font-size: 13px;

    border-radius: 25px;
  }
}

/* Стили для ширины, которая больше 1600px 
  Здесь хранятся правила для устройств с большим экраном. 
*/
@media only screen and (min-width: 1600px) {
  .photo {
    margin: 0 0 0 300px;
    padding: 70px 70px 120px 0;
    max-height: 500px;
  }

  .about-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    margin: 0 300px 0 0;
    padding: 70px 0 120px;
  }

  .about-title {
    padding-bottom: 40px;
    
    font-size: 20px;
  }

  .about-text__first-paragraph {
    padding-bottom: 20px;

    font-size: 20px;
  }

  .about-text__subsequent-paragraph {
    padding-bottom: 20px;

    font-size: 15px;
  }

  .resume-link {
    width: 230px;
    height: 50px;

    font-size: 13px;

    border-radius: 25px;
  }
}

/* Стили, которые совпадают на всех устройствах и во всех ориентациях.*/
.about {
  display: flex;
  color: #edf2f4;

  background-color: #8d99ae;
}

.about-title {
  font-weight: 400;
}

.about-text {
  text-align: justify;
}

.resume-link-container {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}

/*noinspection CssNonIntegerLengthInPixels*/
.resume-link {
  display: flex;
  justify-content: center;
  align-items: center;

  color: #edf2f4;
  letter-spacing: 4.8px;
  font-weight: 200;
  text-transform: uppercase;

  background-color: #8d99ae;
  box-shadow: -7.778px -7.778px 25px 0 rgba(255, 255, 255, 0.3),
                14.142px 14.142px 22.5px 2.5px rgba(43, 45, 66, 0.16);
}

.resume-link:hover {
  color: #edf2f4;
}
